}
.container{
    display: flex;
    flex-direction: column;
    flex-wrap:wrap;
    background-color: #fff;
    padding: 10px;
}
.row{
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
}
.filters{
    background-color: #fff;
}
.filter-details{
    background-color: #fff;
}


.title{
    background-color: #fff;
}
.btn{
    background: transparent;
    background-color: rgb(190, 248, 184);
    border: none;
    padding: 8px 10px;
    border-radius: 5px;
    /* color: rgb(187, 247, 187); */
    margin: 10px;

}

.btn:hover{
    cursor: pointer;
    background-color: rgb(51, 160, 51);
}
.developer{
    text-align: center;
}

*{
    /* margin:0;
    padding:0; */
    box-sizing:border-box;
  }
  
  body{
    margin-top: 50px;
    font-family: "Roboto Mono", monospace;
    /* text-align: center; */
    background: #111;
  }
  
  .Card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .cardItem {
    box-shadow: 1px -1px 30px 0px rgba(0, 0, 0, 0.3);
    background-color: #fff;
    border-radius: 10px;
    margin: 20px;
    padding: 20px;
    min-width: 250px;
    min-height: 250px;
    flex-shrink:0;
  }
  .img{
    text-align: center;
    margin-left: 20px;
  }
  .name{
    color: rgb(22, 22, 207);
  }
  .text{
    font-size: 1.5rem;
    font-weight: 600;
    color: #111;
    margin-bottom: 10px;
    background-color: #fff;
  }
